  /* Skill Set */
  
  #container-out{
    background-color: black;
    padding: 0px;
  }
  
  .container-skill {
    padding: 40px;
    height: 695px;
    max-width: 500px;
    margin: 0 auto;
    background-color: yellow;
  }
  
  #skill-title{
  font-size: 30px;
  font-style:bold;
  font-family: Coronetscript;
  text-align: center;
  }
  
  .progress-bar {
    margin: 0 0 10px;
    overflow: hidden;
    padding-right: 24px; /* Separate bars from container */
  }
  
  .progress-title-holder {
    padding-bottom: 7px;
    position: relative;
    margin: 5px 0;
    font-family: Montserrat, sans-serif;
    font-size: 15px;
    line-height: 15px;
    font-weight: 400;
    color: #2e2e2e;
  }
  
  .progress-title {
    z-index: 100;
  }
  
  .progress-number-wrapper {
    width: 100%;
    z-index: 10;
  }
  
  .progress-number-mark {
    margin-bottom: 4px;
    border-radius: 3px;
    background-color: rgb(22,23,29);
    padding: 0 8px;
    position: absolute;
    bottom: 0;
    -moz-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
  }
  
  .progress-number-wrapper,
  .progress-number-mark {
    font-family: Open Sans, sans-serif;
    font-size: 11px;
    line-height: 24px;
    height: 24px;
    letter-spacing: 0px;
    font-weight: 600;
    font-style: normal;
    text-transform: none;
    color: #ffffff;
  }
  
  .down-arrow {
    border-left: 3px solid transparent;
    border-right: 3px solid transparent;
    border-top: 3px solid #16171d;
    position: absolute;
    left: 50%;
    top: 100%;
    -moz-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
  }
  
  .progress-content-outter {
    height: 12px;
    background-color: #ffffff;
  }
  
  .progress-content {
    height: 21px;
    background-color: rgba(67, 243, 183, .92);
    width: 0%; /* Initial value */
  }
  
  
  /* UP CLose and Personal */
  
  .Personal{
