  }
  #con{
    font-size: 25px;
    color: #FFFFFF;
    font-family: Times New Roman;
  }
  
  /* Picture & Overlay Starts here */
  .pic-container {
    width: 100%;
  }
  .overlay {
    position: absolute;
    top: 0px;
    background: rgb(0, 0, 0);
    background: rgba(0, 0, 0, 0.5); /* Black see-through */
    color: #f1f1f1;
    margin : 10px;
    transition: .5s ease;
    opacity:0;
    color: white;
    font-size: 40px;
    text-align: center;
  }
  
  .vis{
    position: absolute;
    padding: 10px;
    bottom:0px;
    right: 10px;
    background: rgb(0, 0, 0);
    background: rgba(0, 0, 0, 0.5); /* Black see-through */
    color: #f1f1f1;
    margin: 10px;
    transition: .5s ease;
    opacity:0;
    color: white;
    font-size: 40px;
    text-align: center;
  }
  .pic-container:hover .overlay {
    opacity: 1;
  }
  .pic-container:hover .vis {
    opacity: 1;
  }
  
  .pic-container:hover .Imageclass{
    filter: grayscale(100%);
  }
  
  .Imageclass{
    display: block;
    height: auto;
    width: 100%;
  }
  
  /* Picture & Overlay Ends here */